import React from 'react'
import './projects.css'
import ReactPlayer from 'react-player'
import picFive from './photo5.jpg'
//import picFour from './photo4.jpg'
import AR from './AR.png'

const sizeIn = {
  height: 350,
  width: 600
}

const sixeIn = {
  height: 350,
  width: 500 
}

const Home = () => {
    return(
    <body>
      <div className="content-inside">
        <div className="row">
        <div className="column">
          <div class="grey">
           <h4>About Me</h4>
           <p>
             My name is Maika here on my website you'll find all the crazy shit that interest me plus my
             computer programming work. Wasn't really into school that much but rather 
             worked hard my whole life to learn on my own in order to expand my skills. Anyone who
             knows me knows I'm not much of a big talker, I mostly just chill and relax; However at any given random 
             moment the exciting part of me comes out and it's always a good time. I love music, I skate here and there,
             I collect knives :) , I game sometimes, I make art sometimes, I love hiking and camping , 
             I love animals, and I'm down for whatever. Hit me up in my contacts if you a real one. 
           </p>
        </div>
        </div>
        <div className="column">
          <div className="white">
           <h4>AR Project</h4>
           <img src = {AR} alt="AR" style={sizeIn}/>
           <p>Made some progress on my Unity knowledge 
             with the help of another application called
             vuforia. I'm just getting started in
             augmented reality but I'm really enjoying this.
             Just a year ago I never thought I could do this.
           </p>
        </div>
      </div>
      </div>
      <div className="row">
        <div className="column">
          <div class="scale1">
           <h4>Guitar Shit</h4>
          <img src = {picFive} alt="picFive" style={sixeIn}/>
        </div>
        </div>
        <div className="column">
          <div className="scale2">
           <h4>Miscellaneous</h4>
            <ReactPlayer
             url="https://youtu.be/UqrW0-OUExc" />
        </div>
      </div>
      </div>
      </div>
    </body>
    )
}

export default Home;