import React from 'react'
import './finalP.css'
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
    table: {
      minWidth: 650,
    },
  });
  
  function createData(title, description, language) {
    return { title, description, language };
  }
  
  const rows = [
    createData('Amazon & Spotify Music Web Scraping', 'web scraped the top hit songs of the week from Amazon Music and Spotify ', 'Python, MySQL, PHP, HTML'),
    createData('Call Report App', 'organizes different call reports based on client review of the company', 'Bootstrap, Python, Flask, ChartJS'),
    createData('My Personal Website', 'created this website from scratch ', 'ReactJS, material-ui, CSS-Grid, EmailJS'),
    createData('Syntax Analyzer', 'reads a stream of tokens from the lexical anaylzer based on a grammar', 'C++'),
    createData('Python Exam AutoGrader', 'student/teacher login where student takes a python exam that is then autograded. The teacher can review auto graded results and make changes and student can log in to view exam and score', 'PHP, PHPmyadmin, JavaScript, CSS, HTML'),
  ];

const Projects = () => {
    const classes = useStyles();
    return(
    <body>
      <div className="content">
      <div className="content-inside">
           <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
            <TableHead>
                <TableRow>
                    <TableCell>Project</TableCell>
                    <TableCell align="left">Description</TableCell>
                    <TableCell align="left">Language(s)</TableCell>
                </TableRow>
            </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.title}>
              <TableCell component="th" scope="row">
                {row.title}
              </TableCell>
              <TableCell align="left">{row.description}</TableCell>
              <TableCell align="left">{row.language}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
        <div className="row">
        <div className="column">
        <h4>skills and months/years experience</h4>
            <div className="box1">
            <h3 className="skill">Bash</h3>
            <hr></hr>
            <ul>
            <li className="list">6+ months experience</li>
            </ul>
            <h3 className="skill">C++</h3>
            <hr></hr>
            <ul>
              <li className="list">2 years experience</li>
              <li className="list">1 year teaching assistant experience</li>
            </ul>
            <h3 className="skill">Java</h3>
            <hr></hr>
            <ul>
              <li className="list">3 years experience</li>
            </ul>
            <h3 className="skill">Python</h3>
            <hr></hr>
            <ul>
              <li className="list">3 years experience</li>
            </ul>
            <h3 className="skill">JavaScript</h3>
            <hr></hr>
            <ul>
              <li className="list">3 months experience</li>
            </ul >
            <h3 className="skill">React JS</h3>
            <hr></hr>
            <ul>
              <li className="list">3 months experience</li>
            </ul>
            <h3 className="skill">MySQL</h3>
            <hr></hr>
            <ul>
              <li className="list">4 years experience</li>
            </ul>
            <h3 className="skill">Git</h3>
            <hr></hr>
            <ul>
              <li className="list">6+ months experience</li>
            </ul>
            <h3 className="skill">JIRA</h3>
            <hr></hr>
            <ul>
              <li className="list">3 months experience</li>
            </ul>
            <h3 className="skill">Microsoft PowerBI</h3>
            <hr></hr>
            <ul>
              <li className="list">3 months experience</li>
            </ul>
            <h3 className="skill">PHP</h3>
            <hr></hr>
            <ul>
              <li className="list">1 months experience</li>
            </ul>
        </div>
      </div>
      </div>
      </div>
      </div>
    </body>
    )
}

export default Projects;